import React from "react";
import { updateTextCreator, addPostCreator } from "redux/profileReducer";
import AddPost from "./AddPost";

function addPostContainer(props) {
  const profilePage = props.store.getState().profilePage;
  function readSendText(text) {
    props.store.dispatch(updateTextCreator(text));
  }
  function savePost() {
    props.store.dispatch(addPostCreator());
  }

  return (
    <AddPost
      savePost={savePost}
      readSendText={readSendText}
      tempText={profilePage.tempText}
    />
  );
}

export default addPostContainer;
