import React from "react";
import AnotherUserMessage from "./AnotherUserMessage/AnotherUserMessage";
import c from "./Chat.module.scss";
import ChatInputContainer from "./ChatInput/ChatInputContainer";
import SelfMessage from "./SelfMessage/SelfMessage";

function Chat(props) {
  const messagesPage = props.store.getState().messagesPage;

  const messages = messagesPage.messages.map((message) =>
    message.userName ? (
      <AnotherUserMessage
        userName={message.userName}
        text={message.text}
        timeStamp={message.timeStamp}
      />
    ) : (
      <SelfMessage text={message.text} timeStamp={message.timeStamp} />
    )
  );

  return (
    <div className={c.wrapper}>
      <div className={c.chat}>{messages}</div>
      <ChatInputContainer store={props.store} />
    </div>
  );
}

export default Chat;
