import React from "react";
import AddPostContainer from "./AddPost/AddPostContainer";
import MyPosts from "./MyPosts/MyPosts";
import c from "./Profile.module.scss";
import ProfileInfo from "./ProfileInfo/ProfileInfo";

function Profile(props) {
  const profile = props.store.getState().profilePage;
  return (
    <main className="app-wrapper-content">
      <ProfileInfo
        backgroundImg={profile.profileInfo.backgroundImg}
        userPhoto={profile.profileInfo.userPhoto}
        userName={profile.profileInfo.userName}
        userAge={profile.profileInfo.userAge}
        livingAt={profile.profileInfo.livingAt}
        workingAt={profile.profileInfo.workingAt}
      />
      <AddPostContainer store={props.store} />
      <MyPosts posts={profile.posts} />
    </main>
  );
}

export default Profile;
